body {
  background-color: #333; }

#container {
  width: 70vw;
  margin: 30vw auto; }

h1 {
  text-align: center;
  color: #fff;
  font-size: 5em;
  letter-spacing: 8px;
  font-family: 'Press Start 2P', HelveticaNeue, Helvetica, sans-serif;
  font-weight: 400;
  /*Create overlap*/
  margin: 0;
  line-height: 0;
  /*Animation*/
  -webkit-animation: glitch1 2.5s infinite;
          animation: glitch1 2.5s infinite; }

h1:nth-child(2) {
  color: #67f3da;
  -webkit-animation: glitch2 2.5s infinite;
          animation: glitch2 2.5s infinite; }

h1:nth-child(3) {
  color: #f16f6f;
  -webkit-animation: glitch3 2.5s infinite;
          animation: glitch3 2.5s infinite; }
